<template>
	<view class="product">
		<!-- 搜索商品 -->
		<view class="product-search">
			<image class="icon32" src="/static/image/icon_ss.png" mode=""></image>
			<input class="product-search-input" v-model="searchVal" placeholder="搜索商品名称" />
		</view>
		<!-- 商品标签 商品类别 商品信息 -->
		<view class="product-list">
			<!-- 标签 -->
			<view class="searchType flex-center flexd jubetween">
				<view class="item" v-for="(item,i) in statusList" :key="i"
					:class="statusIndex==item.isvalid?'active':''" @click="statusIndex=item.isvalid">
					{{item.status}}
					<image class="icon" src="/static/img/icon_xuanzhong.png" mode="widthFix"></image>
				</view>
			</view>
			<!-- 类别 信息 -->
			<view class="product-list-box">
				<!-- 类别 -->
				<view class="type">
					<view class="type-list" :class="shopTypeIndex==index?'active':''" v-for="(item,index) in shopType"
						:key="index" @click="shopTypeIndex = index">
						<span>{{item.category_name}}</span>

						<image v-show="shopTypeIndex == index&&statusIndex==1"
							@click="shopTypeHandlePop('edit',item.category_name)" src="/static/image/icon_update.png"
							class="edit" mode=""></image>

						<image v-show="shopTypeIndex == index&&statusIndex==1" @click="shopTypeHandlePop('delete')"
							src="/static/img/icon_delete.png" class="delete" mode=""></image>
					</view>
					<view class="type-list new-type" @click="shopTypeHandlePop('add')">
						<image class="icon32" src="/static/img/icon_+.png" mode=""></image>
						<span>新建分类</span>
					</view>
					<view class="add-product save-product" @click="saveEvent">
						<span>保存</span>
					</view>
				</view>
				<!-- 信息 -->
				<view class="product-box">
					<view class="products" v-for="(item,index) in shopType[shopTypeIndex].productVos" :key="index">
						<view class="product-logo">
							<image class="logo" v-if="item.images" :src="item.images" mode=""></image>
							<image class="xiangji" v-else src='/static/img/icon_xiangji.png' mode=""></image>
							<view class="black" v-show="statusIndex==3">
								<span>售罄</span>
							</view>
						</view>
						<view class="product-news">
							<input type="text" v-model="item.name"
								:placeholder="'请输入'+(userinfo.user_type==3?'商品':'服务')+'名称'"
								placeholder-class="placeholder" />
							<view class="stock">
								<span>库存</span>
								<uni-number-box v-model="item.stock" :min="0" :max="999"></uni-number-box>
							</view>
							<input class="price-input" type="text" v-model="'¥ '+item.selling_price" />
							<view class="meth">
								<view class="sale">
									<span class="font22">月销 {{item.month_sale}}</span>
								</view>
								<view class="handel">
									<span class="delete" @click="productHandle('delete',index)">删除</span>
									<span class="shelf" @click="productPopEvent('shelf',index)">下架</span>
								</view>
							</view>
						</view>
					</view>
					<view class="add-product" @click="productHandle('add')">
						<image class="icon32" src="/static/img/icon_+.png" mode=""></image>
						<span>添加商品</span>
					</view>
				</view>
			</view>
		</view>

		<!-- 分类弹出层 -->
		<uni-popup ref="typePopup" type="center">
			<view class="type-popup">
				<view class="popup-add" v-if="handleIndex=='add'">
					<view class="title">
						<span class="font36">新增分类</span>
					</view>
					<view class="valuebox">
						<input class="product-search-input" v-model="addVal" placeholder="请输入分类名称" />
					</view>
				</view>
				<view class="popup-edit" v-else-if="handleIndex=='edit'">
					<view class="title">
						<span class="font36">修改分类</span>
					</view>
					<view class="valuebox">
						<input class="product-search-input" v-model="editVal" placeholder="请输入分类名称" />
					</view>
				</view>
				<view class="popup-delete" v-else>
					<view class="deleteText">
						<span class="font36">确定删除该分类?</span>
					</view>
				</view>
				<view class="type-popup-submit">
					<button type="primary" class="cancel" @click="cancelEvent_type">取消</button>
					<view style="width:2rpx;background-color: #F6F6F6;"></view>
					<button type="primary" class="confirm" @click="confimeEvent_type">确定</button>
				</view>
			</view>

		</uni-popup>

		<!-- 产品弹出层 -->
		<uni-popup ref="productPopup" type="center">
			<view class="product-popup">
				<view class="title">
					<span v-if="productShelf=='shelf'" class="font36">确定下架该商品?</span>
					<span v-else class="font36">确定上架该商品?</span>
				</view>
				<view class="product-popup-submit">
					<button type="primary" class="cancel" @click="cancelEvent_type_product">取消</button>
					<view style="width:2rpx;background-color: #F6F6F6;"></view>
					<button type="primary" class="confirm" @click="confimeEvent_type_product">确定</button>
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				userId: '',
				userinfo: {},
				// 搜索商品
				searchVal: '',
				// 商品分类
				statusList: [{
					status: '已上架',
					isvalid: 1
				}, {
					status: '已下架',
					isvalid: 2
				}, {
					status: '已售罄',
					isvalid: 3
				}],
				statusIndex: 1,
				// 商品列表
				shopTypeIndex: 0,
				shopType: [{
					"category_name": "", //类别名称
					"productVos": [{ //	商品集合
						"id": 0, //编号
						"images": "", //服务/商品图片
						"stock": 0, //库存
						"market_price": 0, //市场价
						"name": "", //服务/商品名称
						"selling_price": 0, //销售价
						"month_sale": 0 //月销量
					}]
				}],

				// 分类的更改/删除/添加 
				handleIndex: '',
				editVal: '',
				addVal: '',
				// 商品的上架/下架
				productShelf: '',
				productIndex: ''
			}
		},
		created() {
			this.getList()
		},
		mounted() {
			this.userId = uni.getStorageSync('userId')
			this.userinfo = uni.getStorageSync('userInfo')
		},
		watch: {
			// 切换商品状态刷新商品列表
			statusIndex(newVal, oldVal) {
				this.getList()
			}
		},
		methods: {
			// 获取商品列表
			getList() {
				let that = this
				this.util.ajax('shop/getShopProducts', {
					userId: that.userId,
					searchType: that.statusIndex
				}, res => {
					that.shopType = res.data.categoryVos
				})
			},
			// 保存商品信息
			saveEvent() {
				let that = this
				this.util.ajax('shop/saveShopProduct', {
					categoryVos: this.shopType[typeIndex]
				}, res => {
					console.log('保存商品', res.data)
				})
			},
			// 商品类别
			// (添加 删除 更改)弹窗
			shopTypeHandlePop(val, name) {
				let that = this
				that.$refs.typePopup.open()
				that.handleIndex = val
				if (name) {
					that.editVal = name
				}
			},
			// 取消
			cancelEvent_type() {
				this.$refs.typePopup.close()
			},
			// 确认
			confimeEvent_type() {
				let that = this
				if (that.handleIndex == 'edit') {
					that.shopType[that.shopTypeIndex].category_name = that.editVal
				} else if (that.handleIndex == 'delete') {
					that.shopType.splice(that.shopTypeIndex, 1)
					that.shopTypeIndex = 0
				} else {
					that.shopType.push({
						"category_name": that.addVal,
						"productVos": [{
							"images": "",
							"isvalid": that.statusIndex,
							"name": "",
							"selling_price": 0,
							"stock": 0,
							"month_sale": 0
						}]
					})
				}
				// 关闭弹窗
				this.$refs.typePopup.close()
			},
			//商品列表
			// (删除 下架 上架)弹窗
			productPopEvent(val, index) {
				let that = this
				that.$refs.productPopup.open()
				that.productShelf = val
				that.productIndex = index
			},
			// 取消
			cancelEvent_type_product() {
				this.$refs.productPopup.close()
			},
			// 确认
			confimeEvent_type_product() {
				let that = this
				if (that.productShelf == 'shelf') {
					that.shopType[that.shopTypeIndex].productVos[that.productIndex].isvalid = 2
				} else {
					that.shopType[that.shopTypeIndex].productVos[that.productIndex].isvalid = 1
				}
				this.$refs.productPopup.close()
			},
			// 商品列表(添加 删除)
			productHandle(val, index) {
				let that = this
				if (val == 'add') {
					that.shopType[that.shopTypeIndex].productVos.push({
						"images": "",
						"isvalid": that.statusIndex,
						"name": "",
						"selling_price": 0,
						"stock": 0,
						"month_sale": 0
					})
				} else if (val == 'delete') {
					that.shopType[that.shopTypeIndex].productVos.splice(index, 1)
				}
			}
		}
	}
</script>

<style lang="less">
	.product-search {
		padding: 20rpx 50rpx;
		background-color: #fff;
		position: relative;

		.icon32 {
			position: absolute;
			top: 48rpx;
			left: 70rpx;
		}

		.product-search-input {
			margin: auto;
			padding: 20rpx 30rpx 20rpx 60rpx;
			border-radius: 120rpx;
			background-color: #F5F5F5;
		}
	}

	.product-list {
		margin-top: 14rpx;

		.searchType {
			padding-top: 1rpx solid #F5F5F5;
			background-color: #fff;
			padding: 8rpx 0;

			.item {
				line-height: 100rpx;
				position: relative;
				font-size: 28rpx;
				text-align: center;
				width: 33.3%;

				&:last-child {
					color: #666666;
				}

				.icon {
					position: absolute;
					bottom: 10rpx;
					left: 50%;
					transform: translateX(-50%);
					width: 30rpx;
					opacity: 0;
				}

				&.active {
					font-size: 32rpx;
					color: #212121;
					font-weight: bold;

					.icon {
						opacity: 1;
					}
				}
			}
		}

		.product-list-box {
			display: flex;

			.type {
				width: 212rpx;
				text-align: center;
				background-color: #F5F6FA;
				padding-top: 10rpx;
				padding-bottom: 110rpx;

				.type-list {
					color: #666666;
					position: relative;
					padding: 40rpx 0;

					image {
						width: 24rpx;
						height: 24rpx;
					}

					.edit {
						position: absolute;
						top: 20rpx;
						right: 20rpx;
					}

					.delete {
						position: absolute;
						bottom: 20rpx;
						right: 20rpx;
					}
				}

				.active {
					background-color: #fff;
				}

				.new-type {
					display: flex;
					justify-content: center;
					align-items: center;

					.icon32 {
						margin-right: 10rpx;
					}
				}
			}

			.product-box {
				flex: 1;
				height: calc(100vh - 370rpx);
				padding: 12rpx 30rpx;
				background-color: #fff;
				overflow-y: scroll;
				padding-bottom: 110rpx;

				.products {
					display: flex;
					justify-content: space-between;
					margin-bottom: 70rpx;

					.product-logo {
						width: 120rpx;
						height: 120rpx;
						text-align: center;
						background-color: #CCCCCC;
						border-radius: 12rpx;
						position: relative;
						overflow: hidden;

						.xiangji {
							width: 60rpx;
							height: 60rpx;
							margin-top: 30rpx;
						}

						.logo {
							width: 100%;
							height: 100%;
						}

						.black {
							width: 100%;
							height: 100%;
							background: rgba(51, 51, 51, 0.5500);
							position: absolute;
							left: 0;
							top: 0;
							display: flex;
							align-items: center;
							justify-content: center;

							span {
								color: #fff;
							}
						}
					}

					.product-news {
						flex: 1;
						padding-left: 25rpx;

						.stock {
							display: flex;
							padding: 20rpx 0;

							span {
								margin-right: 10rpx;
							}
						}

						.price-input {
							color: #FF6000;
							font-size: 42rpx;
						}

						.meth {
							display: flex;
							justify-content: space-between;
							margin-top: 24rpx;

							.sale {
								span {
									color: #666666;
								}
							}
						}

						.handel {
							.delete {
								margin-right: 15rpx;
								padding: 9rpx 20rpx;
								border-radius: 21rpx;
								border: 1px solid #979797;
							}

							.shelf {
								color: #FF6000;
								padding: 9rpx 20rpx;
								border-radius: 21rpx;
								border: 1px solid #FF6000;
							}
						}

						input {
							padding: 6rpx 15rpx;
							background-color: #F5F5F5;
						}
					}
				}

				.add-product {
					width: calc(100vw - 212rpx);
					position: fixed;
					bottom: 0;
					right: 0;
					background-color: #fff;

					.icon32 {
						margin-right: 10rpx;
					}
				}


			}
		}
	}

	.add-product,
	.save-product {
		padding: 29rpx 0;
		display: flex;
		align-items: center;
		justify-content: center;
		box-shadow: 0 -2px 5px -1px #888888;

	}

	.save-product {
		width: 212rpx;
		position: fixed;
		bottom: 0;
		left: 0;
		background: linear-gradient(180deg, #FDEC7E 0%, #F9D448 100%);
	}

	.product-popup {
		width: 530rpx;
		background-color: #fff;
		border-radius: 10rpx;
	}

	.type-popup {
		width: 530rpx;
		background-color: #fff;
		border-radius: 10rpx;

		.popup-add,
		.popup-edit,
		.popup-delete {
			padding: 35rpx 60rpx;

			.title {
				text-align: center;
				margin-bottom: 35rpx;
			}

			.valuebox {
				input {
					padding: 25rpx 35rpx;
					border: 1px solid #CCCCCC;
					border-radius: 10rpx;
				}
			}
		}

		.popup-delete {
			.deleteText {
				padding: 30rpx 0;
				text-align: center;
			}
		}
	}

	.product-popup {
		.title {
			padding: 65rpx 0;
			text-align: center;
		}
	}

	.product-popup-submit,
	.type-popup-submit {
		display: flex;
		border-top: 1px solid #F6F6F6;

		.cancel {
			flex: 1;
			background-color: #fff;
			color: #0091FF;
			border-right: 1prx solid #F6F6F6;
		}

		.confirm {
			flex: 1;
			background-color: #fff;
			color: #0091FF;
			border-left: 1prx solid #F6F6F6;
		}
	}
</style>
